import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { TransactionModuleRoutingModule } from './transaction-module-routing.module';
import { TransactionModuleComponent } from './transaction-module.component';
import { FormsModule } from '@angular/forms';
import { CoreModule } from 'core/core.module';


@NgModule({
  declarations: [TransactionModuleComponent],
  imports: [
    CoreModule,
    CommonModule,
    FormsModule,
    TransactionModuleRoutingModule
  ]
})
export class TransactionModuleModule { }



