import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Constants } from 'core/constants';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { LocalStorageService } from 'core/service/local-storage.service';
import { UserSession } from 'core/sessions';
import * as xlsx from 'xlsx';
import { forEach } from '@angular/router/src/utils/collection';
import { Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter } from 'rxjs/operators';
import { ActivitiesListData } from 'core/models/activitiesListResponse';
import { UtilService } from 'ngx-mydatepicker/dist/services/ngx-my-date-picker.util.service';
import { BaseComponent } from 'app/base/base.component';


declare var $: any;

@Component({
  selector: 'app-transaction-module',
  templateUrl: './transaction-module.component.html',
  styleUrls: ['./transaction-module.component.css']
})
export class TransactionModuleComponent extends BaseComponent implements OnInit,HttpSuccesFailureResponse {
  @ViewChild('trxntable') trxntable: ElementRef;

  startDate: any="2020/12/25";
  endDate:any="2020/12/25";
  headerTitleGYMRegular=[{name:"Date & Time",view:false,id:0},{name:"Reference no.",view:false,id:0},{name:"Customer Name",view:false,id:0},{name:"Child Name",view:false,id:0},{name:"Customer Email",view:false,id:0},{name:"Phone",view:false,id:0},{name:"Item",view:false,id:0},{name:"Expiry",view:false,id:0},{name:"Amount",view:false,id:0},{name:"Done By",view:false,id:0},{name:"Action",view:false,id:0}]
  newpaymentData = [{"title":"energy class","price":0.0,"trainerName":"tison kumar","classDate":"2020-12-02","fromTime":"18:00:00","toTime":"19:00:00","priceModel":"CHARGABLE","numberOfParticipants":1,"dropInVisitors":0,"generalVisitors":2,"ptVisitors":0,"workshopVisitors":0,"gymRateCardList":[],"videoClass":false},{"title":"energy class","price":0.0,"trainerName":"tison kumar","classDate":"2020-12-01","fromTime":"18:00:00","toTime":"19:00:00","priceModel":"CHARGABLE","numberOfParticipants":1,"dropInVisitors":0,"generalVisitors":1,"ptVisitors":0,"workshopVisitors":0,"gymRateCardList":[],"videoClass":false},{"title":"energy class","price":12.0,"trainerName":"tison kumar","classDate":"2020-12-16","fromTime":"18:00:00","toTime":"19:00:00","priceModel":"CHARGABLE","numberOfParticipants":1,"dropInVisitors":1,"generalVisitors":1,"ptVisitors":0,"workshopVisitors":0,"gymRateCardList":[],"videoClass":false},{"title":"energy class","price":0.0,"trainerName":"tison kumar","classDate":"2020-12-17","fromTime":"18:00:00","toTime":"19:00:00","priceModel":"CHARGABLE","numberOfParticipants":1,"dropInVisitors":0,"generalVisitors":1,"ptVisitors":0,"workshopVisitors":0,"gymRateCardList":[],"videoClass":false}];
  userType;
  listIsLaoding;
  transactionsDataFromApiTotal:any=[];
  transactionsData:any=[];
  transactionsDataFromApi:any=[];

 currencyCode:any;
 totrev=0;
 searchText="";
 currentPage = 0;
 countTotalRev=true;
 countTotalRevMap=new Map<number, boolean>();

 searchDecouncer$: Subject<string> = new Subject();

 appliedPackageFilter = [];
 filteredList = [];
 gymId:any;
 userId:any;
 transactionAmount:any;
 selectedTransaction:any;
 totalCount: any=0;
 transactionType: any='auto';

  ngAfterViewInit() {
    var that=this;
    $(function() {
      $('input[name="daterange"]').daterangepicker({ 
        locale: {
            format: 'YYYY/MM/DD'
        },
        startDate: that.startDate,
        endDate:  that.endDate,
        maxDate:new Date(),
        minDate:"2020/12/25",
    });
  });
 

  $('input[name="daterange"]').on('apply.daterangepicker', function(ev, picker) {
that.dateRangeChange(picker.startDate._d,picker.endDate._d);
});

    $(document).ready(function() {
        window.alert = function(){};
    });

  }


  constructor(private route: ActivatedRoute,private router: Router,private http_userauth: HttpUserAuthUtilsService) {
    super();
   }
  
  ngOnInit(): void {
    this.loadAdminDataIfLoggenIn();

    this.currencyCode = LocalStorageService.getCurrencyCode();

    this.userType = LocalStorageService.getUserType();
    this.gymId = LocalStorageService.getUserId();
    this.http_userauth.getAllMembershipPackages(this,true,this.gymId);

    //this.http_userauth.getMembershipPackages(this.gymId, this,true,"GYMSPECIFIC");
    this.route.queryParams.subscribe(qparams=>{
      if(qparams['userId']){
        this.userId = qparams['userId'];
      }
      this.route.params.subscribe(params => {
        if(params['fromDate']){
          this.startDate = UserSession.getStartDate(params['fromDate']) || null;}else{
         // this.startDate = UserSession.getStartDate(new Date());
          this.startDate = UserSession.getStartDate(new Date(
            new Date().getFullYear(),
            new Date().getMonth() - 1, 
            new Date().getDate()
        ));
        }
        if(params['toDate']){this.endDate = UserSession.getStartDate(params['toDate']) || null;}
        else{
          this.endDate = UserSession.getStartDate(new Date());
        }
        
        
        
        this.getTransactionList();
        this.setupSearchDebouncer();
        });
    })
    
  }


  dateRangeChange(start,end){
    this.startDate=UserSession.getStartDate(start);
    this.endDate=UserSession.getStartDate(end);
    if(LocalStorageService.getUserType()==2){
      this.router.navigate(['trainer/transaction',this.startDate,this.endDate]);
    }else{
      this.router.navigate(['gym/transaction',this.startDate,this.endDate]);
  
  }
  this.totrev=0;
  this.countTotalRev =true;
  this.searchText="";
  return;
  }


  getTransactionList(){
   if(this.userId){
      this.http_userauth.getTransactionHistoryUserWise(this.userId,this,true,this.transactionType);

   }else{
    this.http_userauth.getTransactionHistory(this.startDate , this.endDate,this.currentPage,this.searchText,this.appliedPackageFilter,this,true,this.transactionType);

   }
    
    }


  // getTransactionListTotal(){
 
  // let startDate = "2020-12-25";
  // let endDate = UserSession.getStartDate(new Date());
 
  //  this.http_userauth.getTransactionHistoryTotal(startDate ,endDate,this,true);
    
  //   }

  switchTransactionType(){
    if(this.transactionType=="manual"){
      this.transactionType="auto";
    } else{
      this.transactionType="manual";
    }
    this.getTransactionList();
  }
    onSuccess(type: any, responsedata: any) {
      if (type == Constants.getPackageListApi) { 
        this.filteredList = responsedata.data; ;

       }   
       if(type == Constants.transactionHistoryUpdate){
        this.getTransactionList();
       }
      if(type==Constants.transactionHistoryAPI||type==Constants.transactionHistoryUserWise){

       this.transactionsDataFromApi = responsedata.data;
        this.transactionsData=this.transactionsDataFromApi;
        console.log(responsedata);
        if(responsedata.totalAmount){
          this.totrev=responsedata.totalAmount;

        }
        if(this.currentPage==0){
          this.totalCount = responsedata.totalCount;
        }
        
       // if(!this.countTotalRevMap.get(this.currentPage)){
        // this.transactionsData.forEach( (element)=>{
        //   this.totrev += element.amount;
        // }

        // );
      //}
       this.countTotalRevMap.set(this.currentPage,true);
       this.transactionsData.forEach(element => {
      let isPast = UserSession.checkisPastDate(element.expireDate)
      if(isPast){
        if(element.balanceClasses&&element.balanceClasses>0){
          element.status=element.balanceClasses+" classes expired - ("+element.expireDate+")"

        }else{
          element.status="Expired - ("+element.expireDate+")"

        }
      }else{
        element.status= "Active - expires ("+element.expireDate+")"
      }
      if(element.finishedDate){
        var dateobj=new Date(element.finishedDate)
        

        element.status="Finished ("+UserSession.getStartDate(dateobj)+")"
       
      }
      if(this.transactionType=="manual"){
        element.status='-'      }
        
       });
      }
     

      if(type==Constants.transactionHistoryAPITotal){
        this.transactionsDataFromApiTotal = responsedata.data;
        console.log(this.transactionsDataFromApiTotal)
      }
    }
    onFailure(type: any, response: any) {
     
    }
    onFilterApply(event)
    {
    //   this.totrev=0;
    //  let  localtransactionsData:any=[];
      
    //   if(this.searchText&&this.searchText.length>0){
    //     this.transactionsDataFromApiTotal.forEach(element => {
    //       let fullname = element.user;
    //       let mobile = element.mobile;
    //       let email = element.email;
          

    //       if((fullname&&fullname.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1) || (mobile&&mobile.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1)
    //       ||(email&& email.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1) ){
    //         localtransactionsData.push(element);
    //       }
          
    //     });
    //     this.transactionsData = localtransactionsData;
    //   }else{
    //     this.transactionsData = this.transactionsDataFromApi;

    //   }
    //   this.transactionsData.forEach( (element)=>{
    //     this.totrev += element.amount;
    //   }

    //   );
    this.searchDecouncer$.next(this.searchText);

     
   } 
   onPageChange(event){
      
    console.log(event);
    if(this.currentPage>event-1){
      this.countTotalRev = false;
    }else{
      this.countTotalRev = true;

    }
    
    this.currentPage = event-1;


    this.getTransactionList();
     }
    downloadExcel() {
      const ws: xlsx.WorkSheet =   
      xlsx.utils.table_to_sheet(this.trxntable.nativeElement);
      const wb: xlsx.WorkBook = xlsx.utils.book_new();
      xlsx.utils.book_append_sheet(wb, ws, 'Sheet1');
      xlsx.writeFile(wb, 'transactionExcel.xlsx');
     }
     private setupSearchDebouncer(): void {
      this.searchDecouncer$.pipe(
        debounceTime(500),
        distinctUntilChanged(),
      ).subscribe((term: string) => {
        this.currentPage = 0;
        this.totrev  = 0;
        this.countTotalRev =true;
        this.searchText = term;
        this.getTransactionList();
      

      });
    }
    selectPackage(event){
      let selectedPackage=event.target.value;
      if(selectedPackage=="MANUAL"){
        this.appliedPackageFilter = [];

        this.transactionType="manual";
        
      }
     else if(selectedPackage=="-1"){
      this.transactionType="auto";

        this.appliedPackageFilter = [];
      }else{
        this.transactionType="auto";

        this.filteredList.forEach(element => {
          if(element.id==selectedPackage){
            this.appliedPackageFilter.push(element)
          }
        });
      }
    
      this.getTransactionList();
  
     }
  
  
     removeSelectedPackage(event){
      this.appliedPackageFilter =   this.appliedPackageFilter.filter((element) => {
        if(element.id!=event.id){
          return true;
        }
  
  });
  this.getTransactionList();
  
     } 
     editTransaction(item?:any){
      if(item){
        this.selectedTransaction = item;
        this.transactionAmount = item.amount;
        $('#modelWindowBook').modal('show');

      }

     }
     updateTransaction(){
      this.http_userauth.updateTransaction(this.selectedTransaction ,this.transactionAmount,this,true);
  

     }
     addTransaction(){
      this.router.navigate(['manualTransaction']); 

     }
  
}



